/*----- BASIC -----*/
html, body{
  height: auto;
}
body {
  font-family: 'Metropolis Regular';
  font-weight: 400;
  background: #fff;
  color: #000;
  line-height: 1.5;
  font-size: 16px;
  position: relative;
  overflow-x: hidden;
  transition: all ease 0.5s;
  letter-spacing: 0.5px;
}
a, a:hover{
  color: #fff;
  transition: ease-in-out 0.5s;
  text-decoration: none;
}
ul{
  list-style: none;
}
h1,h2,h3{
  font-family: 'Metropolis Bold';
}
h4,h5,h6{
  font-family: 'Metropolis Medium';
}
.favicon{
  width: 16px;
  height: 16px;
  object-fit: contain;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
input, select, textarea, button {
  box-shadow: none;
  outline: none;
  background: none;
}
body::-webkit-scrollbar-track
{
  background-color: #C4C4C4;
  border-radius: 0px;
}

body::-webkit-scrollbar
{
  width: 2px;
  background-color: #F5F5F5;
}

body::-webkit-scrollbar-thumb
{
  border-radius: 0px;
  background: transparent;
}


/*----- MAIN -----*/
.ds-head .wd-btn-box-log a:first-child{
  display: block!important;
}
#liberate-cont {
  background: url(/liberate/static/media/home-bg.6c394ebf.jpg) no-repeat center center;
  background-size: 100% auto;
  height: 100vh;
  display: flex;
  align-items: center;
}
.libe-content h1 {
  color: #fff;
  font-size: 60px;
  margin: 0;
}
.libe-content p {
  margin: 20px 0 25px 0;
  color: #fff;
  font-size: 16px;
  line-height: 30px;
}
.libe-content a {
  font-size: 15px;
  border: solid 2px #fff;
  border-radius: 50px;
  padding: 0 30px;
  line-height: 31px;
  display: inline-block;
  font-family: 'Metropolis Medium';
}
.libe-content a:hover {
  background: #fff;
  color: #000;
}
.libe-content span {
  display: block;
  margin: 15px 0 0 0;
  font-size: 12px;
  color: #fff;
}
/* Contact us start */
.inner-baner {
  position: relative;  
}
.inner-baner .contact-banner-img {
  display: block;
  width: 100%;
}
.banner-cont-main {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 100%;
}
.inner-baner-cont h1 {
  color: #fff;
  margin: 0 0 10px 0;
  font-size: 38px;
  font-family: 'Metropolis Extra Bold';
}
.inner-baner-cont p {
  font-size: 18px;
  color: #fff;
  margin: 0;
}
.contact-content-sect {
  padding: 75px 0;
}
.contact-main-cont {
  position: relative;
  width: 100%;
}
.contact-map {
  width: 100%;
}
.contact-map iframe {
  display: block;
  width: 100%;
  height: 650px;
}
.contact-form {
  position: absolute;
  top: 50px;
  left: 0;
  width: 550px;
  background: #FFFFFF;
  box-shadow: 2px 3px 21px rgba(0, 0, 0, 0.08);
  border-radius: 0px 13px 13px 0px;
  padding: 30px 40px;
}
.contact-form span {
  font-size: 16px;
  text-transform: uppercase;
  font-family: 'Metropolis Medium';
  background: linear-gradient(to right, #70BE2A, #C2D429);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.contact-form h2 {
  font-size: 36px;
  text-transform: uppercase;
  color: #000;
  margin: 15px 0 40px 0;
}
.c-form .form-group {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
}
.c-form .form-group .form-box {
  position: relative;
  width: 48%;
}
.c-form .form-group .form-box input, .c-form .form-group .form-box textarea {
  height: 45px;
  border-radius: 6px;
  border: solid 1px #E2E2E2;
  padding: 0 40px 0 10px;
  font-size: 14px;
  color: #000;
  font-family: 'Metropolis Medium';
  letter-spacing: 0.5px;
  width: 100%;
  display: block;
  background: #F3F3F3;
}
.c-form .form-group .form-box i {
  position: absolute;
  right: 10px;
  top: 15px;
  font-size: 16px;
  color: #979797;
}
.form-group.massage .form-box {
  width: 100%;
}
.c-form .form-group .form-box textarea {
  resize: none;
  padding: 10px 40px 10px 10px;
  height: 140px;
}
.green-btn {
  color: #fff;
  background: linear-gradient(90deg, #70BE2A 0%, #C2D429 100%);
  box-shadow: 0px 0px 4px rgba(143, 209, 30, 0.3);
  border-radius: 42px;
  border: 0;
  font-size: 15px;
  text-transform: uppercase;
  font-family: 'Metropolis Medium';
  padding: 10px 40px;
}
.c-form .form-group:last-child {
  margin-bottom: 0;
}
.c-form .form-group .form-box input:focus, .c-form .form-group .form-box textarea:focus {
  background: #fff;
  border-image-source: linear-gradient(to left, #C2D429, #70BE2A);
  border-image-slice: 1;
}
/* Faq start */
.faq-content {
    margin-top: 75px;
    width: 100%;
}
.cont-title {
    margin-bottom: 40px;
}
.cont-title span {
    font-size: 14px;
    text-transform: uppercase;
    font-family: 'Metropolis Regular';
}
.cont-title h2 {
    font-size: 30px;
    text-transform: uppercase;
    margin: 10px 0 0 0;
}
.faq-content .card {
    background: #FFFFFF;
    box-shadow: 0px 4px 14px rgba(0, 0, 0, 0.08);
    border: 0;
    margin-bottom: 15px;
    border-radius: 16px;
}
.faq-content .card:last-child {
    margin-bottom: 0;
}
.faq-content .card .card-header {
    background: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 16px;
    border: 0;
    padding: 0;
}
.faq-content .card .card-header h5 {
  width: 100%;
}
.faq-content .card .card-header .btn {
    font-size: 17px;
    color: #000;
    font-family: 'Metropolis Regular';
    text-decoration: none;
    outline: none;
    box-shadow: none;
    padding: 0 0 0 25px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 0;
}
.faq-content .card .card-header i {
    font-size: 24px;
    line-height: 75px;
    width: 75px;
    height: 75px;
    text-align: center;
    border-radius: 16px;
    background: linear-gradient(24.11deg, #70BE2A -28.78%, #C2D429 97.04%);
    color: #fff;
}
.faq-content .card .card-header .btn.collapsed i:before {
  content: '\f067';
}
.faq-content .card .card-body {font-size: 15px;font-family: 'Metropolis Regular';opacity: 0.7;border-top: solid 1px #ccc;padding: 30px 25px;}
.faq-contact .contact-form {
    position: static;
    border-radius: 13px;
}
.contact-content-sect.faq-contact {
    padding-bottom: 30px;
}
.faq-contact .contact-main-cont {
    display: flex;
    justify-content: space-between;
}
.faq-image {
    position: relative;
    bottom: -30px;
}
/* Pricing plan start */
.pricing-content-sect {
    padding: 75px 0;
}
.pricing-title {
    text-align: center;
}
.pricing-main-cont {
    width: 100%;
}
.pricing-title h2 {
    font-size: 36px;
    margin-bottom: 10px;
}
.pricing-title p {
    margin: 0;
    font-size: 16px;
    color: #000;
    opacity: 0.7;
    line-height: 22px;
}
.pricing-card-main {
    margin-top: 50px;
    display: flex;
}
.pricing-card {
    background: #FFFFFF;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.05);
    border-radius: 22px;
    text-align: center;
}
.pricing-card .card-head {background: #F6F7F9;border-radius: 18px 18px 4px 4px;padding: 30px 0 70px 0;}
.pricing-card .card-head h4 {
    font-size: 26px;
    font-family: 'Metropolis Bold';
    line-height: normal;
    margin-bottom: 5px;
}
.pricing-card .card-head p {
    font-size: 16px;
    margin: 0;
    opacity: 0.5;
}
.pricing-icon {
    background: #FFFFFF;
    box-shadow: 0px 2px 11px rgba(0, 0, 0, 0.05);
    width: 90px;
    height: 90px;
    /*border: solid 2px #fff;*/
    border-radius: 50%;
    text-align: center;
    line-height: 86px;
    margin: -45px auto 0;
    position: relative;
}
.card-cont {
    padding-bottom: 40px;
}
.card-cont h3 {
    font-family: 'Metropolis Medium';
    font-size: 40px;
    margin: 20px 0;
}
.card-cont .green-btn {
    display: inline-block;
    background: #AEAEAE;
    box-shadow: 0px 0px 4px rgba(174, 174, 174, 0.3);
    border-radius: 42px;
    line-height: 42px;
    padding: 0 25px;
    font-size: 15px;
    text-transform: capitalize;
    transition: inherit;
}
.card-cont ul {
    margin: 0 0 25px 0;
    display: inline-block;
}
.card-cont ul li {
    text-align: left;
    font-size: 15px;
    align-items: center;
    display: flex;
    margin: 5px 0;
}
.card-cont ul li img {
    margin-right: 10px;
}
.card-cont h3 sub {opacity: 0.6;font-size: 16px;font-family: 'Metropolis Bold';}
.card-cont h3 span {
    font-size: 16px;
    font-family: 'Metropolis Regular';
    opacity: 0.6;
}
.pricing-card.moderate {
    margin-top: 50px;
}
.pricing-card:hover .green-btn {
    background: linear-gradient( 
90deg
 , #70BE2A 0%, #C2D429 100%);
    box-shadow: 0px 0px 4px rgb(143 209 30 / 30%);
    padding: 0 40px;
}
.pricing-card .pricing-icon:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    border-radius: 50px;
    padding: 2px;
    background: linear-gradient(45deg,#fff,#fff);
    -webkit-mask: linear-gradient(#fff 0 0) content-box, 
     linear-gradient(#fff 0 0);
    -webkit-mask-composite: destination-out;
    mask-composite: exclude;
}
.pricing-card:hover {
  background: #FFFFFF;
  box-shadow: 0px 5px 10px rgba(143, 209, 30, 0.2);
}
.pricing-card:hover .pricing-icon:before {
    background: linear-gradient(45deg, #70BE2A, #C2D429);
    -webkit-mask: linear-gradient(#fff 0 0) content-box, 
     linear-gradient(#fff 0 0);
}
.pricing-icon img {
    display: none;
}
.pricing-card:hover .pricing-icon img {
  display: inline-block;
}
.pricing-card:hover .pricing-icon svg {
  display: none;
}
.pricing-card.moderate .card-head {
  background: url(/liberate/static/media/moderate.2b9b6bfc.png) no-repeat center center;
  background-size: cover;
}
.pricing-card.moderate .card-head h4 {
  color: #fff;
}
.pricing-card.moderate .card-head p {
  color: #fff;
  opacity: 0.9;
}
/* login start */
