.social a {
    color: #545454;
    font-size: 15px;
    margin-right: 30px;
    text-align: center;
}
.social a.facebook i {
    font-size: 24px;
    background: linear-gradient(180deg, #18ACFE 0%, #0163E0 99.7%);
    border-radius: 50%;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
}
.social a.google i {
    width: 25px;
    height: 25px;
    background: #FFFFFF;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
    font-size: 18px;
    border-radius: 50px;
    line-height: 25px;
}
.login-form .form-row.action {
    margin-bottom: 30px;
}
.social {
    margin: 20px 0 25px 0;
}
.social a.facebook span, .social a.google span {background: linear-gradient(180deg, #18ACFE 0%, #0163E0 99.7%);width: 25px;height: 25px;line-height: 25px;display: inline-block;border-radius: 50px;}
.social a.google span {
    background: #FFFFFF;
    box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.12);
}
