    background: #fff;
    border-radius: 20px;
    padding: 20px 15px;
    margin-top: 30px;
}
.profile-detail a img {
    width: 120px;
    height: 120px;
}
.profile-detail .pro-text {
    padding-left: 30px;
}
.profile-detail .pro-text h2 {
    font-size: 24px;
    margin-bottom: 10px;
    line-height: normal;
}
.profile-detail .pro-text p {
    margin: 0;
    font-size: 16px;
    line-height: 24px;
    color: #000;
    opacity: 0.4;
}
.profile-btn {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}
.profile-btn a {
    background: linear-gradient(90deg, #2C575C 9.14%, #65A3A9 95.27%);
    border-radius: 90px;
    text-align: center;
    color: #fff;
    font-size: 16px;
    font-family: 'Metropolis Medium';
    line-height: 42px;
    width: 150px;
}
.profile-btn a i {
    margin-right: 10px;
}
.profile-btn a:hover {
    background: linear-gradient(90deg, #65A3A9 9.14%, #2C575C 95.27%);
}
.profile-btn a.logout-btn {
    margin-top: 20px;
    background: linear-gradient(90deg, #DD6960 0%, #E99089 100%);
    display: inline-block;
}
.profile-btn a.logout-btn:hover {
    background: linear-gradient(90deg, #E99089 0%, #DD6960 100%);
}
.profile-tab-cont {
    margin-top: 20px;
}
.profile-tab-cont .tab-head {
    border-radius: 0;
    padding: 0 15px;
    border-bottom: solid 1px #C4C4C4;
    justify-content: normal;
}
.profile-tab-cont .tab-head .nav-tabs li a {
    position: relative;
    line-height: normal;
    padding: 0 25px 10px;
    font-size: 20px;
    border-bottom: solid 3px transparent;
}
.profile-tab-cont .tab-head .nav-tabs li a.active, .profile-tab-cont .tab-head .nav-tabs li a:hover {
    background: transparent;
    color: #000;
    border-bottom: solid 3px #2e5b60f2;
    border-radius: 0;
}
.p-f-detail {
    padding: 50px 0;
}
.u-d-row {
    display: flex;
    margin: 0 0 30px 0;
}
.u-d-row .u-d-box {
    width: 50%;
    padding-right: 15px;
}
.u-d-row .u-d-box label {
    line-height: normal;
    margin: 0;
    font-size: 15px;
    color: #545454;
    opacity: 0.6;
}
.u-d-row .u-d-box p {
    font-size: 15px;
    color: #545454;
    opacity: 0.9;
    font-family: 'Metropolis Medium';
    margin: 5px 0 0 0;
}
.u-d-row:last-child {
    margin: 0;
}
.plan-box {max-width: 330px;text-align: center;}
.plan-row.current {
    background: #2C575C;
    border-radius: 35px 35px 0 0;
    padding: 30px 0;
}
.plan-row.expired {
    background: #65A3A9;
    border-radius: 0 0 35px 35px;
    padding: 20px 0;
}
.plan-row.current p, .plan-row.expired p {
    color: #fff;
    font-size: 22px;
    line-height: 22px;
    font-family: 'Metropolis Medium';
    margin-bottom: 15px;
}
.plan-row.current strong, .plan-row.expired strong {
    font-size: 22px;
    line-height: normal;
    color: #FFD201;
    font-family: 'Metropolis Semi Bold';
}
.plan-row.current strong sub {
    bottom: 0;
    font-size: 16px;
}
.lessons {padding-top: 10px;direction: rtl;}
.lessons .lessons-box {
    margin-top: 30px;
    max-width: 75%;
    background: linear-gradient(89.92deg, #2C575C 8.41%, #65A3A9 93.01%);
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 12px;
    display: flex;
    direction: ltr;
    align-items: center;
    padding: 20px 0;
}
.lessons-img {
    margin-left: -50px;
}
.lessons-text p {
    color: #fff;
    font-size: 20px;
    font-family: 'Metropolis Medium';
    margin: 0 0 10px 0;
    line-height: normal;
}
.lessons-text {
    padding: 0 30px;
}
.lessons-text strong {
    font-size: 70px;
    line-height: normal;
    color: #fff;
    font-family: 'Metropolis Medium';
    font-weight: normal;
}
.d-footer {
    background: #252625;
    border-radius: 20px 0px 0px 0px;
    padding: 20px 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.f-social {
    display: flex;
}
.f-social p {
    margin: 0;
    color: #fff;
    font-size: 15px;
}
.d-footer ul {
    margin: 0;
    display: flex;
}
.d-footer ul li {
    margin-left: 15px;
}
.d-footer ul li a {
    font-size: 16px;
    color: #fff;
    opacity: 0.6;
}
.d-footer ul li a:hover {
    opacity: 1;
    color: #FFD201;
}
.f-menu ul li {
    margin-left: 30px;
}
.f-menu ul li a {color: #fff;opacity: 0.6;font-size: 13px;font-family: 'Metropolis Medium';}
.f-menu ul li a:hover, .f-menu ul li a.active{
  color: #FFD201;
  opacity: 1;
}
.edit-form .u-d-box:last-child {
    padding-left: 15px;
    padding-right: 0;
}
.edit-form .u-d-box input {background: #F5F5F5;border: 1px solid rgba(0, 0, 0, 0.1);border-radius: 50px;height: 42px;width: 100%;margin-top: 10px;padding: 0 15px;font-size: 15px;font-family: 'Metropolis Medium';}
.edit-form .u-d-row.action {
    direction: rtl;
}
.edit-form .u-d-row.action button {
    font-size: 16px;
    color: #fff;
    border: 0;
    background: linear-gradient(180deg, #2C575C 5.22%, #65A3A9 100%);
    border-radius: 90px;
    line-height: 42px;
    width: 150px;
}
.edit-form .u-d-row.action button:hover {
    background: linear-gradient(180deg, #65A3A9 5.22%, #2C575C 100%);
}
.p-f-table {
    border: solid 1px #EDEDED;
    display: flex;
}
.p-f-t-blog.delete {
    width: 15%;
    text-align: center;
}
.p-f-t-blog.purchase, .p-f-t-blog.expired {
    width: 30%;
}
.p-f-t-blog.plan {
    width: 40%;
}
.p-f-t-row {
    font-size: 16px;
    color: #545454;
    padding: 10px 10px;
    border-right: solid 1px #EDEDED;
}
.p-f-t-row.title {
    background: linear-gradient(180deg, #2C575C 5.22%, #65A3A9 100%);
    color: #fff;
    font-family: 'Metropolis Medium';
}
.p-f-t-blog.delete .p-f-t-row.title {
    height: 44px;
}
.p-f-t-blog.delete .delete-icon {
    color: #F47272;
    font-size: 16px;
}
.p-f-t-blog.delete .delete-icon:hover {
    color: #386368;
}


