    width: 320px;
    border: none;
    height:52px;
    display: block;
        padding: 0.375rem 0.75rem;
    margin-bottom: 1rem;
}
#login-popup .wd-model-body .form-group input:-ms-input-placeholder{
    
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    color: #545454;
    opacity: 0.6;
}
#login-popup .wd-model-body .form-group input::placeholder{
    
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 15px;
    color: #545454;
    opacity: 0.6;
}
#login-popup .wd-model-body .wd-eye-psw{
    position: relative;
}
#login-popup .wd-model-body .wd-eye-psw input{
    padding-right: 41px;
}
#login-popup .wd-model-body .wd-eye-psw a i{
    position: absolute;
    top: 19px;
    right: 14px;
    color:#222C53;
    opacity: 0.2;
}
#login-popup .wd-model-body .form-group button.wd-btn-login-pop{
    background: linear-gradient(90deg, #FFCC21 0%, #FFBE22 100%);
    border-radius: 8px;
  
    font-style: normal;
    font-weight: bold;
    font-size: 16px;
    line-height: 30px;
    text-transform: uppercase;
    color: #FFFFFF;
    border: none;
    width: 320px;
    padding: 7px 0;
    box-shadow: 0px 9px 30px rgb(255 191 34 / 40%);
}
#login-popup .wd-model-body a.wd-for-psw{
  
    font-style: normal;
    font-weight: 500;
    font-size: 13px;
    line-height: 30px;
    color: #16BDC0;
    text-align: center;
    display: block;
}
#login-popup .wd-model-body p{
  
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 30px;
    color: #545454;
    text-align: center;
}
#login-popup .wd-model-body p a{
  
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 30px;
    color: #16BDC0;

}

