.checkbox-group-blocks-label-wrap ul {
  padding: 0px;
  margin: 0px;
  display: flex;
  list-style: none;
  justify-content: space-between;
}
.checkbox-group-blocks-label-wrap {
        width: 75%;
       margin: 0 auto 30px;
}
   .checkbox-group-blocks-label-wrap li {
      position: relative;
      display: inline-block;
      height: 2em;
      width: 100%;
      max-width: 3em;
      line-height: 2em;      
      color: black;
      text-align: center;
      }
    .checkbox-group-blocks-label-wrap  label {
        position: absolute;
        left: 0;
        width: 100%;
      }
      
    .checkbox-group-blocks-label-wrap  .label-text {
        background-color: whitesmoke;
        position: absolute;
        left: 0;
        width: 100%;
        border: 1px solid black;
        border-right: none;        
        -webkit-user-select: none;        
            -ms-user-select: none;        
                user-select: none;
        transition: all 0.1s ease-in-out;
      }
      
    
        .checkbox-group-blocks-label-wrap .label-text {
          border-radius: 5px 0px 0px 5px;
        }
     
        .checkbox-group-blocks-label-wrap .label-text {
          border-right: 1px solid black;
          border-radius: 0px 5px 5px 0px;
        }
      
      .checkbox-group-blocks-label-wrap input[type="checkbox"] {
        //display: none;    
      }
      .checkbox-group-blocks-label-wrap ul li .label-text {
          background: #C4C4C4;
          border-radius: 50%;
          position: absolute;
          left: 0;
          padding: 7px 0;
          width: 100%;
          border: none;
          border-right: none;
          -webkit-user-select: none;
              -ms-user-select: none;
                  user-select: none;
          transition: all 0.1s ease-in-out;
      }
      .checkbox-group-blocks-label-wrap ul li input[type=checkbox]:checked + .label-text {
          background: #65A6AE;
          color: white;
          animation: push 0.2s 1 ease-out;
      }
        
@keyframes push {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(0.8);
  }
  100% {
    transform: scale(1);
  }
}

